import React, { Component } from "react";
import { connect } from "react-redux";
import axios from "../../../axios-orders";
import withErrorHandler from "../../../hoc/withErrorHandler/withErrorHandler";
import * as actions from "../../../store/actions/index";

import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";

import Input from "../../../components/UI/Input/Input";
import Spinner from "../../../components/UI/Spinner/Spinner";
import "./ContactData.css";

class ContactData extends Component {
  state = {
    orderForm: {
      name: {
        elementType: "input",
        elementConfig: {
          type: "text",
          placeholder: "Your Name",
        },
        value: "",
        label: "Name",
        validation: { required: true },
        valid: false,
      },

      street: {
        elementType: "input",
        elementConfig: {
          type: "text",
          placeholder: "Your Street",
        },
        value: "",
        label: "Street",
        validation: { required: true },
        valid: false,
      },
      zipcode: {
        elementType: "input",
        elementConfig: {
          type: "text",
          placeholder: "Your Zip Code",
        },
        value: "",
        label: "Zip Code",
        validation: { required: true, minLength: 5, maxLength: 5 },
        valid: false,
      },
      country: {
        elementType: "input",
        elementConfig: {
          type: "text",
          placeholder: "Your Country",
        },
        value: "",
        label: "Country",
        validation: { required: true },
        valid: false,
      },

      email: {
        elementType: "input",
        elementConfig: {
          type: "email",
          placeholder: "Your Email",
        },
        value: "",
        label: "Email",
        validation: { required: true },
        valid: false,
      },
      deliveryMethod: {
        elementType: "select",
        elementConfig: {
          options: [
            { value: "fastest", displayValue: "Fastest" },
            { value: "cheapest", displayValue: "Cheapest" },
          ],
        },
        value: "fastest",
        validation: {},
        label: "Delivery Method",
      },
    },
    loading: false,
  };

  checkValidity(value, rules) {
    let isValid = true;
    if (rules.required) {
      isValid = value.trim() !== "" && isValid;
    }
    if (rules.minLength) {
      isValid = value.length >= rules.minLength && isValid;
    }
    if (rules.maxLength) {
      isValid = value.length <= rules.maxLength && isValid;
    }
    return isValid;
  }

  orderHandler = (event) => {
    event.preventDefault();
    const formData = {};
    for (let formEleId in this.state.orderForm) {
      formData[formEleId] = this.state.orderForm[formEleId].value;
    }
    const order = {
      ingredients: {
        ...this.props.cnt,
        seeds: this.props.sds,
      },
      price: this.props.price,
      orderData: formData,
      userId: this.props.userId
    };
    this.props.onOrderBurger(order, this.props.token);
  };

  inputChangedHandler = (event, inputId) => {
    const updatedOrderForm = {
      ...this.state.orderForm,
    };
    const updatedFormElement = { ...updatedOrderForm[inputId] };
    updatedFormElement.value = event.target.value;
    updatedFormElement.valid = this.checkValidity(
      updatedFormElement.value,
      updatedFormElement.validation
    );

    updatedOrderForm[inputId] = updatedFormElement;
    this.setState({ orderForm: updatedOrderForm });
  };

  render() {
    const formElementsArray = [];
    for (let key in this.state.orderForm) {
      formElementsArray.push({ id: key, config: this.state.orderForm[key] });
    }

    let form = (
      <Form onSubmit={this.orderHandler}>
        {formElementsArray.map((formElement) => {
          let required = false;
          let minLength = "";
          let maxLength = "";
          if (formElement.config.validation !== undefined) {
            if (formElement.config.validation.required) {
              required = true;
            }
            if (formElement.config.validation) {
              if (formElement.config.validation.minLength) {
                minLength += formElement.config.validation.minLength.toString();
              }
              if (formElement.config.validation.maxLength) {
                maxLength = formElement.config.validation.maxLength.toString();
              }
            }
          }
          return (
            <Input
              key={formElement.id}
              label={formElement.config.label}
              elementType={formElement.config.elementType}
              elementConfig={formElement.config.elementConfig}
              value={formElement.config.value}
              required={required}
              invalid={!formElement.config.valid}
              minLength={minLength}
              maxLength={maxLength}
              changed={(event) =>
                this.inputChangedHandler(event, formElement.id)
              }
            />
          );
        })}
        <Button
          className="spaced"
          id="button-order"
          type="submit"
          variant="success"
        >
          ORDER
        </Button>
      </Form>
    );
    if (this.props.loading) {
      form = <Spinner />;
    }
    return (
      <div className="ContactData">
        <h4>Enter your contact details.</h4>
        {form}
      </div>
    );
  }
}

const matchStatetoProps = (state) => {
  return {
    sds: state.burger.ingredients.seeds,
    cnt: state.burger.count,
    price: state.burger.totalPrice,
    loading: state.order.loading,
    token: state.auth.token,
    userId: state.auth.userId
  };
};

const matchDispatchtoProps = (dispatch) => {
  return {
    onOrderBurger: (orderData, token) =>
      dispatch(actions.purchaseBurger(orderData, token)),
  };
};

export default connect(
  matchStatetoProps,
  matchDispatchtoProps
)(withErrorHandler(ContactData, axios));
