import React from "react";

import Button from "react-bootstrap/Button";
import Burger from "../../BurgerMeal/Burger/Burger";

import "./CheckoutSummary.css";

const CheckoutSummary = (props) => {
  return (
    <div className="CheckoutSummary">
      <h1>We hope it tastes well!</h1>

      <div className="container-burger-sm">
        <Burger sm ingredients={props.ingredients} count={props.count} />
      </div>

      <Button
        className="spaced"
        id="button-cancel-checkout"
        variant="success"
        onClick={props.cancelled}
      >
        <span>CANCEL</span>
      </Button>
      <Button
        className="spaced"
        id="button-continue"
        variant="success"
        onClick={props.continued}
      >
        <span>CONTINUE</span>
      </Button>
    </div>
  );
};

export default CheckoutSummary;
